.wrapper {
  padding: 4rem 3rem;
  height: 593px;
  display: grid;
  column-gap: 30px;
  grid-template-columns: auto auto;
  background-color: rgba(33, 33, 33, 0.97);
  color: goldenrod;
}

.profile {
  height: 30vw;
  border-radius: 0;
  animation-name: profile;
  animation-duration: 0.5s;
}

.profile:hover {
  border-radius: 10%;
  animation-name: profileHover;
  animation-duration: 0.5s;
}

@keyframes profileHover {
  from {
    border-radius: 0;
  }
  to {
    border-radius: 10%;
  }
}

@keyframes profile {
  from {
    border-radius: 10%;
  }
  to {
    border-radius: 0;
  }
}

@media (max-width: 600px) {
  .wrapper {
    grid-template-columns: auto;
    row-gap: 0;
  }
}
